/* js파일에 관련된 스타일 작성 */
@font-face {
  font-family: 'TAEBAEKfont';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2310@1.0/TAEBAEKfont.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Happiness-Sans-Bold';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2205@1.0/Happiness-Sans-Bold.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: 'NanumSquareNeo-Variable';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_11-01@1.0/NanumSquareNeo-Variable.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}

html {
  background: url('img/background_img.jfif') center center / cover no-repeat;
  background-attachment: fixed;
  position: relative;
}

.header, nav {
  max-width: 767px;
  width: 100%;
  height: 80px;
  border-radius: 50px;
  position: fixed;
  left: 0;
  text-align: center;
  line-height: 120px;
  z-index: 99999999;
}
header {
  top: 30px;
  background-color: rgba(0, 0, 255, 0.253);
}
nav {
  bottom: 30px;
  background-color: #0000007c;


}
.header figure {
  height: 45px;

}
.header figure img {
  height: 100%;
}

h3 {
  margin-top: 150px;
  width: 100%;
  padding: 30px 0;
  text-align: center;
  font-size: 42px;
  font-family: 'TAEBAEKfont';
  color: #ffffffda;
}
.tour_list {
  max-width: 767px;
  width: calc(100% - 60px);
  margin: 0 auto;
  .item {
    position: relative;
    height: 300px;
    margin: 20px auto;
    border-radius: 10px;
      overflow: hidden;
    figure {
      width: 100%;
      height: 100%;
      border-radius: 10px;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    
      h4 {
        position: absolute;
        top: 0;
        left: 0;
        margin: 70px;
        font-size: 20px;
        color: rgba(255, 255, 255, 0.8);
        font-family: 'Happiness-Sans-Bold';
        z-index: 1;
    }
    .item--text {
      p {
        padding: 40px 70px;
        position: absolute;
        top: 0;
        left: 0;
        color: rgba(255, 255, 255, 0.8);
        font-family: 'Happiness-Sans-Bold';
        
      }
      span {
        position: absolute;
        bottom: 0;
        right: 0;
        margin: 15px;
        padding: 10px;
        background-color: rgba(255, 255, 255, 0.637);
        border-radius: 10px;
        padding: 8px;
        display: block;
        font-family: 'NanumSquareNeo-Variable';
        font-size: 20px;

      }
      span::before {
        content: '#';
      }
      
    }
  }
  .item figure::before {
    content: '';
    width: 100%;
    height: 100%;
    background: rgb(0,0,0);
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.5) 0%, rgba(255,255,255,0) 60%);
    position: absolute;
    top: 0;
    left: 0;
    display: block;
  }
  .item:hover h4 {
    color: #fff;
  }
  .item:hover figure::before {
    background: rgb(0,0,0);
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.541) 0%, rgba(255,255,255,0) 30%);
  }
}
